<template>
  <div>
    <cms-breadcrumb to="/after/service/list" type="list">
      <!-- <li>{{$route.name}}</li> -->
      <li>{{'售后服务单-'+typeName(afterOrderItem.orderRefund.refundType)+'详情'}}</li>
    </cms-breadcrumb>
    <div class="cms-list-layout" v-loading="loading" v-cloak v-if="load"> 
      <!-- 退款退货详情 -->
      <el-row class="form-warp">
        <el-col :span="8" class="form-inner">
          <el-form label-width="120px">
            <el-form-item label="售后服务单：">{{afterOrderItem.orderRefund.orderRefundNum}}</el-form-item>
            <el-form-item label="订单号：">{{afterOrderItem.order.orderCode}}</el-form-item>
            <div class="after-after-status">{{afterOrderItem.orderRefund.status| refundStatusFilter}}</div>
            <div style="text-align:center;">
              <el-button
                type="primary"
                style="width:120px;"
                v-if="afterOrderItem.orderRefund.status==0"
                @click="checkService(afterOrderItem,true)"
              >确认</el-button>
              <el-button
                type="primary"
                style="width:120px;margin-bottom:10px;"
                v-if="afterOrderItem.orderRefund.status==0"
                @click="checkService(afterOrderItem,false)"
              >确认不通过</el-button>
              <el-button
                type="primary"
                style="width:120px"
                v-if="afterOrderItem.orderRefund.status==5"
                @click="checkService(afterOrderItem,true)"
              >确认收货</el-button>
              <br>
              <el-button
                size="mini"
                type="primary"
                style="width:120px"
                v-if="afterOrderItem.orderRefund.status==7"
                @click="checkService(afterOrderItem,true)"
              >发货</el-button>
            </div>
            
          </el-form>
        </el-col>
        <el-col :span="16" class="border-left form-inner after-flow-right">
          <!-- 流程部分 -->
          <div class="after-process-wrap">
            <div class="after-process-item"  v-if="item.operateType!==4"
            v-for="(item,index) in afterOrderItem.orderRefundDetails" :key="index">
                <div class="after-processlogo-wrap">
                  <i class="el-icon-success after-process-logo"></i>
                </div>
                <div class="after-process-line" 
                v-if="index!==afterOrderItem.orderRefundDetails.length-1||afterOrderItem.orderRefund.status<8"
                ></div>
                <div class="after-process-info">
                  <span class="after-process-title">{{operateFilter(item.operateType)}}</span> 
                  <span v-if="true">{{$moment(item.createTime)}}</span>
                </div>
            </div>
            <div v-if="afterOrderItem.orderRefund.status==0" class="after-process-item after-processitem-dark">
              <div class="after-processlogo-wrap">
                <i class="el-icon-success after-process-logo after-processlogo-dark"></i>
              </div>
              <div class="after-process-line after-processline-dark"></div>
              <div class="after-process-info">
                <span class="after-process-title">卖家确认</span>
              </div>
            </div>
            <div v-if="afterOrderItem.orderRefund.status<8" class="after-process-item after-processitem-dark">
              <div class="after-processlogo-wrap">
                <i class="el-icon-success after-process-logo after-processlogo-dark"></i>
              </div>
              <div class="after-process-info">
                <span class="after-process-title">完成</span>
              </div>
            </div>
          </div>
          <!-- 物流部分 -->
          <div class="after-flow-main" >
            <div class="after-flow-item" v-for="(item,index) in refundDetails" :key="index">
              <div class="after-flow-itemmain">
                <div>
                  <img src="@/assets/images/circle.png" width="14" height="14" v-if="index!=0">
                   <img src="@/assets/images/dot-circle-o.png" width="14" height="14" v-if="index==0">
                </div>
                <span class="after-flow-time" >{{$moment(item.createTime)}}</span>
                <span class="after-flow-position" :class="{'after-flow-bold':index==0?true:false}">
                  {{item.operateTxt}}
                </span>
              </div>
            </div>
          </div>
        </el-col>
      </el-row>
      <el-row class="form-warp mt-20">
        <el-col :span="8" class="form-inner">
          <h3 class="after-info-title">售后服务单信息</h3>
          <el-form label-width="100px">
            <el-form-item label="申请人：">
              {{afterOrderItem.orderRefund.member.nickname}}
              ({{afterOrderItem.orderRefund.member.username}} | {{afterOrderItem.orderRefund.member.phone}})
            </el-form-item>
            <el-form-item label="申请时间：">{{$moment(afterOrderItem.orderRefund.applyTime)}}</el-form-item>
            <el-form-item label="货物状态：">{{afterOrderItem.orderRefund.productStatus==1?'未收到货':'已收到货'}}</el-form-item>
            <el-form-item label="服务类型：">{{afterOrderItem.orderRefund.refundType | refundTypeFilter}}</el-form-item>
            <el-form-item label="原因类型：">{{afterOrderItem.orderRefund.refundReason}}</el-form-item>
            <el-form-item class="t-word" label="问题描述：">{{afterOrderItem.orderRefund.customerTxt}}</el-form-item>
            <el-form-item label="凭证：">
              <img
               class="refundImg"
                v-for="(imgs,index) in afterOrderItem.orderRefund.pics"
                :key="index"
                :src="$cms.filterUrl(imgs.picResource.url)"
              />
            </el-form-item>
          </el-form>
        </el-col>
        <!-- 只有refundType==2,3 才有 -->
        <el-col :span="8" class="form-inner border-left"
         v-if="afterOrderItem.orderRefund.refundType!=1">
          <div style="min-height:120px;border-bottom:1px dashed #eee">
            <h3 class="after-info-title">买家发货物流信息</h3>
            <el-form label-width="100px" v-if="afterOrderItem.orderRefund.sellerAddress">
              <el-form-item label="配送地址：">
                <p>{{afterOrderItem.orderRefund.sellerAddress}}</p>
                <p>{{afterOrderItem.orderRefund.sellerName}} （ {{afterOrderItem.orderRefund.sellerTel}} ）</p>
                </el-form-item>
              <el-form-item label="运送方式："
              v-if="afterOrderItem.orderRefund.customerExpressCompany"
              >{{afterOrderItem.orderRefund.customerExpressCompany.cnName}}</el-form-item>
              <el-form-item label="运单号：">
                {{afterOrderItem.orderRefund.customerExpressNum}}
                <el-popover placement="top" width="700" trigger="click">
                  <ul class="after-flow__items">
                    <li v-for="(item,index) in saleFlowList" :key="index" class="after-flow__item">
                      <span class="after-flow__time">
                        <i class="el-icon-time"></i>
                        {{item.acceptTime}}
                      </span>
                      <span class="after-flow__content">{{item.acceptStation}}</span>
                    </li>
                  </ul>
                  <span slot="reference" class="after-info-viewdes">查看详细</span>
                </el-popover>
              </el-form-item>
            </el-form>
          </div>
          <div  style="min-height:120px;" v-if="afterOrderItem.orderRefund.refundType===3">
            <h3 class="after-info-title">卖家发货物流信息</h3>
            <el-form label-width="100px" v-if="afterOrderItem.orderRefund.sellerExpressNum">
              <el-form-item label="配送地址：">
                <p>{{afterOrderItem.orderRefund.customerAddress?afterOrderItem.orderRefund.customerAddress:'-'}}</p>
                <p>{{afterOrderItem.orderRefund.customerName}} （ {{afterOrderItem.orderRefund.customerTel}} ）</p>
                </el-form-item>
              <el-form-item label="运送方式："
              v-if="afterOrderItem.orderRefund.sellerExpressCompany"
              >{{afterOrderItem.orderRefund.sellerExpressCompany.cnName?afterOrderItem.orderRefund.sellerExpressCompany.cnName:'-'}}</el-form-item>
              <el-form-item label="运单号：">
                {{afterOrderItem.orderRefund.sellerExpressNum?afterOrderItem.orderRefund.sellerExpressNum:'-'}}
                <el-popover placement="top" width="700" trigger="click">
                  <ul class="after-flow__items">
                    <li v-for="(item,index) in sellFlowList" :key="index" class="after-flow__item">
                      <span class="after-flow__time">
                        <i class="el-icon-time"></i>
                        {{item.acceptTime}}
                      </span>
                      <span class="after-flow__content">{{item.acceptStation}}</span>
                    </li>
                  </ul>
                  <span slot="reference" class="after-info-viewdes">查看详细</span>
                </el-popover>
              </el-form-item>
            </el-form>
          </div>
        </el-col>
        <el-col :span="8" class="form-inner border-left" style="min-height:310px;">
          <h3 class="after-info-title">平台审核结果</h3>
          <div class="after-audit-wrap">
            <div class="after-audit-logo">
              <i class="el-icon-warning"></i>
            </div>
            <div class="after-audit-info">
              如果买家于卖家就售后服务单协商未果，导致卖家未确认售后服务单相关信息，
              平台将作为协商方介入，最终会由平台确认结果
            </div>
          </div>
          <el-form label-width="100px" v-if="afterOrderItem.orderRefund.platformIntervent">
            <el-form-item label="审核时间：">{{$moment(afterOrderItem.lastIntervent.createTime)}}</el-form-item>
            <el-form-item
              label="审核结果："
            >{{afterOrderItem.lastIntervent.platformStatus|platformStatusFilter}}</el-form-item>
            <el-form-item label="备注：">{{afterOrderItem.lastIntervent.interventResult}}</el-form-item>
          </el-form>
        </el-col>
      </el-row>
      <table class="order-table mt-20">
        <tr>
          <th>商品</th>
          <th>单价</th>
          <th>数量</th>
          <th>退款明细</th>
        </tr>
        <tbody>
          <tr>
            <td class="product-info">
              <div class="product__inner">
                <img class="product-img" :src="$cms.filterUrl(afterOrderItem.product.coverImage)">
                <div class="product-title" v-if="afterOrderItem.product.name">
                  <h3 class="title-h3">{{afterOrderItem.product.name}}</h3>
                  <p
                    class="title-p"
                    style="margin-top:10px;"
                    v-if="afterOrderItem.proSkuAttrList"
                  >{{showAttr(afterOrderItem.proSkuAttrList)}}</p>
                </div>
              </div>
            </td>
            <td class="order-count">
              <span>￥{{$cms.float(afterOrderItem.orderItem.productPrice)}}</span>
            </td>
            <td class="order-count">x{{afterOrderItem.orderItem.productCount}}</td>
            <td class="order-count" v-if="afterOrderItem.orderRefund.refundType!==3">
              <div>
                <span class="order-count-named">积分:</span>
                <span
                  class="order-count-value"
                  v-if="afterOrderItem.orderRefund.refundIntegral"
                >{{afterOrderItem.orderRefund.refundIntegral}}</span>
                <span class="order-count-value" v-if="!afterOrderItem.orderRefund.refundIntegral">0</span>
              </div>
              <div>
                <span class="order-count-named">红包:</span>
                <span
                  class="order-count-value"
                  v-if="afterOrderItem.orderRefund.refundRedPacket"
                >{{$cms.float(afterOrderItem.orderRefund.refundRedPacket)}}</span>
                <span class="order-count-value" v-if="!afterOrderItem.orderRefund.refundRedPacket">￥0.00</span>
              </div>
              <div>
                <span class="order-count-named">退款金额:</span>
                <span class="order-count-value">￥{{$cms.float(afterOrderItem.orderRefund.refundAmount)}}</span>
              </div>
              <div>
                <span class="order-count-named">退款方式:</span>
                <span class="order-count-value">{{afterOrderItem.order.payMode|payModeFilter}}返还</span>
              </div>
            </td>
            <td class="order-count" v-else>
                <div>无</div>
              </td>
          </tr>
        </tbody>
      </table>
      <!-- 下頭部分 -->
      <check-service ref="checkService" @change="changeService($route.query.id)"></check-service>
    </div>
  </div>
</template>
<script>
import mixin from './mixin'
import checkService from './checkService'
import { refundGet } from './../../../api/shop/order.js'
export default {
  mixins: [mixin],
  components: {
    checkService
  },
  data() {
    return {
      load: false
    }
  },
  methods: {
    operateFilter(val) { // 售后流程
      var arr = ['提交申请', '卖家同意', '卖家拒绝', '申请撤回', '平台介入处理中', '买家已发货', '卖家确认收货', '卖家已发货', '买家确认收货', '修改售后申请', '', '', '平台审核通过', '平台审核拒绝']
      return arr[parseInt(val)]
    },
    getStatus(val) {
      const STATUS_CODE = [0, 2, 4, 5, 1, '', 8]
      return STATUS_CODE.indexOf(val)
    },
    getTheOrder(id) {
      this.loading = true
      refundGet({
        id: id
      }).then(res => {
        if (res.code === 200) {
          if (res.data.orderRefundDetails.length > 0) {
            var details = []
            res.data.orderRefundDetails.forEach(v => {
              details.push(v)
            })
            this.refundDetails = details.reverse()
          }
          this.afterOrderItem = res.data
          this.loading = false
          this.load = true
          if (res.data.orderRefund.status >= 5) { // 获取物流信息
            this.getFlowInfo(res)
          }
        } else {
          this.$errorMessage(res.message)
          this.loading = false
        }
      })
    },
    typeName(val) {
      var arr = ['退款', '退款退货', '换货']
      return arr[parseInt(val) - 1]
    }
  },
  created() {
    this.getTheOrder(this.$route.query.id)
  }
}
</script>

<style  scoped>
.form-warp {
  border: 1px solid #e0e0e0;
}
.form-inner {
  padding: 20px;

}
.boder-left {
  border-left: 1px solid #e0e0e0;
}
.plat-info {
  font-size: 14px;
}
.el-icon-warning{
  color: #ff0000;
}
.refundImg {
  width: 60px;
  height: 60px;
  background: #eee;
}
.order-count-named {
  display: inline-block;
  width: 70px;
  text-align: right;
  margin-right: 10px;
}
.order-count-value {
  display: inline-block;
  min-width: 80px;
  text-align: left;
  color: red;
}

</style>



