<template>
  <div>
    <el-dialog title="收货地址" :visible="addressVisable" :before-close="handleClose" width="900px">
      <div style="padding:15px;">
        <cms-prompt style="margin-bottom:15px;">
          <li>如确认买家的售后申请，即表示同意买家的售后服务申请，请选择以下回寄地址，买家将商品回寄指定的地址。</li>
        </cms-prompt>
        <div style="min-height:150px" v-loading="addressLoading">
          <el-radio-group v-model="serviceParams.addressId" style="width:100%">
            <ul>
              <li v-for="item in addressdData" :key="item.id" class="addr-items">
                <el-radio :label="item.id">
                  {{item.provinceName}}{{item.cityName}}{{item.areaName}}{{item.address}}
                  &nbsp; &nbsp; &nbsp; &nbsp;
                  {{item.customerName}} {{item.tel}}
                </el-radio>
              </li>
            </ul>
          </el-radio-group>
        </div>
      </div>
      <span slot="footer" class="dialog-footer">
        <el-button @click="addressVisable = false" size="mini">取 消</el-button>
        <el-button type="primary" @click="checkReturns" size="mini">确 定</el-button>
      </span>
    </el-dialog>
    <el-dialog :visible="visible" title="订单发货" :before-close="handleClose" width="900px">
      <div style="padding:15px" v-loading="addressLoading">
        <el-form :model="deliveryForm" ref="deliveryForm" label-width="120px" :rules="rules">
          <el-form-item label="买家收货地址">{{deliveryForm.address}}</el-form-item>
          <el-form-item label="物流公司" prop="expressCompanyId">
            <el-select v-model="deliveryForm.expressCompanyId">
              <el-option
                :label="item.cnName"
                :value="item.id"
                v-for="item in companyList"
                :key="item.id"
              ></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="物流单号" prop="expressNum">
            <el-input v-model="deliveryForm.expressNum"></el-input>
          </el-form-item>
        </el-form>
      </div>
      <div slot="footer" class="dialog-footer">
        <el-button @click="handleClose">取 消</el-button>
        <el-button type="primary" @click="deliverySave">确 定</el-button>
      </div>
    </el-dialog>
  </div>
</template>
<script>
import {
  refundChcek,
  storeAddressPage,
  refundReceive,
  refundDeliver,
  logisticscompanyList
} from './../../../api/shop/order.js'
import rules from '@/rules'
export default {
  data() {
    return {
      visible: false,
      addressVisable: false, // 确认按钮对应弹出框的显示隐藏
      addressLoading: false,
      addressdData: [], // 发货地址集合
      serviceParams: {
        id: '',
        checked: false,
        checkReason: '',
        addressId: ''
      },
      deliveryForm: {
        address: '',
        id: '',
        expressNum: '',
        expressCompanyId: ''
      },
      companyList: [],
      rules: {
        expressCompanyId: [rules.required('此项必填', 'change')],
        expressNum: [rules.required()]
      }
    }
  },
  methods: {
    checkService(data, status) {
      // 响应对应的操作
      // type 售后类型 id 售后id status 是否确认 refundStatus  售后状态
      const type = parseInt(data.orderRefund.refundType)
      const refundStatus = parseInt(data.orderRefund.status)
      const id = data.orderRefund.id
      if (status) {
        // 同意售后
        if (type === 1) {
          // 仅退款
          this.refundPrice(id)
        } else {
          if (refundStatus === 0) {
            this.checkServiceAddress(id)
          }
          if (refundStatus === 5) {
            // 退货退款 已收到货
            this.refundPriceToGoods(id, type)
          }
          if (refundStatus === 7) {
            // 卖家发货
            this.getLogisticscompanyList()
            this.deliveryForm.id = id
            this.deliveryForm.address =
              data.orderRefund.customerAddress +
              ' ' +
              data.orderRefund.customerName +
              ' ' +
              data.orderRefund.customerTel
            this.visible = true
          }
        }
      } else {
        this.refusedService(id) // 不通过
      }
    },
    refundPriceToGoods(id, type) {
      // 商家确认收货
      const info =
        type === 2
          ? '请确保收到买家回寄的商品是否符合，确认收货完成，系统将进行退款操作，是否继续？'
          : '请确保收到买家发送的商品是否符合，确认收货完成，后续无法进行修改，是否继续？'
      this.$confirm(info, '系统提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      })
        .then(res => {
          this.serviceParams.id = id
          this.serviceParams.checked = true
          const params = {
            id: id,
            received: true,
            reason: '卖家已经收到货'
          }
          refundReceive(params).then(res => {
            // 发送确认
            if (res.code === 200) {
              this.$successMessage('操作成功')
              this.$emit('change', true)
            } else {
              this.$emit('change', false)
              this.$errorMessage(res.message)
            }
          })
        })
        .catch(res => {
          return false
        })
    },
    refundPrice(id) {
      // 仅退款操作
      this.$confirm(
        '确认通过后，货款将退至买家原支付账户中，后续无法撤回，确认是否继续？',
        '系统提示',
        {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }
      )
        .then(res => {
          this.serviceParams.id = id
          this.serviceParams.checked = true
          this.refundChcek(this.serviceParams)
        })
        .catch(res => {
          return false
        })
    },
    checkReturns() {
      // 确认退货，确认换货
      this.refundChcek(this.serviceParams)
    },
    deliverySave() {
      // 商家发货
      // 提交订单修改信息
      this.$refs.deliveryForm.validate(valid => {
        if (valid) {
          console.log(JSON.stringify(this.deliveryForm))
          this.loading = true
          refundDeliver(this.deliveryForm).then(res => {
            this.loading = false
            if (res.code === 200) {
              this.$successMessage('操作成功')
              this.visible = false
              this.$emit('change', true)
            } else {
              this.$errorMessage(res.message)
              this.$emit('change', false)
            }
          })
        }
      })
    },
    refusedService(id) {
      // 拒绝售后申请
      this.$prompt(
        '如确认不通过买家的售后申请，申请将驳回，买家可申请平台介入，为避免后续问题，建议与买家协商确认结果。是否继续？',
        '系统提示',
        {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          inputValidator: value => {
            if (value && value !== '') {
              return !(value.length > 140)
            } else {
              return true
            }
          },
          inputType: 'textarea',
          inputErrorMessage: '说明过长',
          inputPlaceholder: '请输入说明原因'
        }
      )
        .then(({ value }) => {
          this.serviceParams.id = id
          this.serviceParams.checked = false
          this.serviceParams.checkReason = value
          this.refundChcek(this.serviceParams)
        })
        .catch(() => {})
    },
    checkServiceAddress(id) {
      // 选择发货地址
      this.serviceParams.id = id
      this.serviceParams.checked = true
      this.addressVisable = true
      this.getAddressPage()
    },
    refundChcek(params) {
      // 发送审核
      refundChcek(params).then(res => {
        this.addressLoading = true
        if (res.code === 200) {
          this.$successMessage('操作成功')
          this.$emit('change', true)
        } else {
          this.$emit('change', false)
          this.$errorMessage(res.message)
        }
        this.addressLoading = false
        this.addressVisable = false
      })
    },
    getAddressPage() {
      // 获取卖家收货地址列表
      if (this.addressdData.length <= 0) {
        this.addressLoading = true
        storeAddressPage({ addressType: 2 }).then(res => {
          if (res.code === 200) {
            this.addressdData = res.data
            res.data.forEach(v => {
              // 获取默认选中的项  把id存起来
              if (v.isDefault) {
                this.$set(this.serviceParams, 'addressId', v.id)
              }
            })
          } else {
            this.$errorMessage(res.message)
          }
          this.addressLoading = false
        })
      }
    },
    getLogisticscompanyList() {
      // 获取物流公司
      logisticscompanyList().then(res => {
        if (res.code === 200) {
          this.companyList = res.data
        }
      })
    },
    handleClose() {
      this.addressVisable = false
      this.visible = false
    }
  }
}
</script>

 <style lang="scss" scoped>
.addr-items {
  width: 100%;
  padding: 10px 15px;
  border-bottom: 1px solid #e0e0e0;
}
</style>



