import { refundFlowInfo } from './../../../api/shop/order.js'
export default {
  data() {
    return {
      afterOrderItem: {},
      loading: false,
      flowList: '', // 物流信息
      refundDetails: [], // 当前状态
      saleFlowList: [], // 买家发货地址
      sellFlowList: [] // 卖家发货地址
    }
  },
  filters: {
    refundStatusFilter(status) {
      const typeArr = ['待卖家确认', '退款中', '卖家拒绝', '', '待买家发货', '待卖家收货', '待买家收货', '待卖家发货', '已完成', '买家已取消']
      return typeArr[status]
    },
    refundTypeFilter(type) { // 收货服务类型过滤器
      const typeArr = ['仅退款', '退货退款', '换货']
      return typeArr[parseInt(type) - 1]
    },
    payModeFilter(type) { // 返回方式
      const modeArr = ['支付宝', '微信', '银联', '余额']
      return modeArr[parseInt(type) - 1]
    },
    platformStatusFilter(status) { // 平台介入处理状态
      const statusArr = ['待确定', '确认通过', '确认不通过']
      return statusArr[parseInt(status)]
    }
  },
  methods: {
    getFlowInfo(res) {
      refundFlowInfo({ id: res.data.orderRefund.id }).then(flowInfo => {
        if (flowInfo.code === 200) {
          flowInfo.data.forEach(v => {
            if (v.userType === 2) {
              this.saleFlowList = v.traces.reverse()
            } else {
              this.sellFlowList = v.traces.reverse()
            }
          })
        } else {
          this.$errorMessage(res.message)
        }
      })
    },
    changeService(id) {
      this.getTheOrder(id)
    },
    checkService(item, status) {
      // 执行售后操作
      this.$refs.checkService.checkService(item, status)
    },
    showAttr(data) {
      // 转换json字符串属性
      if (data.length <= 0) {
        return ''
      } else {
        let str = ''
        data.forEach(v => {
          const tmp = v.attrName + ':' + v.attrValueName + '  '
          str = str + tmp
        })
        return str
      }
    }
  }
}



